import containerEvent from '../libs/containerEvent'
import tools from '../libs/tools'

export default {
  methods: {
    topBarEvent(eventName) {
      this.eventName = eventName

      switch (eventName) {
        case 'cut':
          this.eventCut()
          break;

        case 'rect':
          this.eventRect()
          break;

        case 'text':
          this.eventText()
          break;

        case 'zoomOut':
          this.eventZoomOut()
          break;

        case 'zoomIn':
          this.eventZoomIn()
          break;

        case 'rotateRight':
          this.eventRotateRight()
          break;

        case 'rotateLeft':
          this.eventRotateLeft()
          break;

        case 'rotateMinRight':
          this.eventRotateMinRight()
          break;

        case 'rotateMinLeft':
          this.eventRotateMinLeft()
          break;

        case 'restore':
          this.$emit('restore')
          break;

        case 'clear':
          this.eventClear()
          break;

        case 'done':
          this.eventDone()
          break;
      }
    },

    // 
    eventCut() {
      this.setContextsSelectable(false)

      this.isRect = false
      this.isText = false
      this.isCut = !this.isCut

      this.canvas.requestRenderAll()
    },

    // 
    eventRect() {
      this.clearCutKlass()
      this.setContextsSelectable(false)

      this.isCut = false
      this.isText = false
      this.isRect = !this.isRect

      this.canvas.requestRenderAll()
    },
    
    // 
    eventText() {
      this.clearCutKlass()
      this.setContextsSelectable(false)

      this.activeIndex = -1

      this.isCut = false
      this.isRect = false
      this.isText = !this.isText

      this.canvas.requestRenderAll()
    },

    // 
    eventZoomIn() {
      this.scale = containerEvent.zoomIn(this.params)
      this.scaling = true
      this.angling = false

      this.limitZoomIn()

      this.$emit('zoom', this.scale)

      this.transformContainer()

      this.canvas.requestRenderAll()
    },

    // 
    eventZoomOut() {
      this.scale = containerEvent.zoomOut(this.params)
      this.scaling = true
      this.angling = false

      this.limitZoomOut()

      this.$emit('zoom', this.scale)

      this.transformContainer()

      this.canvas.requestRenderAll()
    },

    // 
    eventZoomOrigin() {
      this.scale = containerEvent.zoomOrigin(this.params)
      this.scaling = true
      this.angling = false

      this.$emit('zoom', this.scale)

      this.transformContainer()

      this.canvas.requestRenderAll()
    },

    // 
    eventRotateRight() {
      this.angling = true
      this.scaling = false

      tools.throttle(() => {
        this.rotateCanvas('RIGHT', void 0, true)
        this.canvas.requestRenderAll()
      })
    },

    // 
    eventRotateLeft() {
      this.angling = true
      this.scaling = false
      
      tools.throttle(() => {
        this.rotateCanvas('LEFT', void 0, true)
        this.canvas.requestRenderAll()
      })
    },

    //  
    eventRotateMinRight() {
      this.rotate += this.angle
      this.container.style['transform-origin'] = '50% 50% 0'
      this.container.style.transform = `rotate(${ this.rotate }deg)`
      this.container.style.transition = 'transform .16s ease-out'
    },

    // 
    eventRotateMinLeft() {
      this.rotate -= this.angle
      this.container.style['transform-origin'] = '50% 50% 0'
      this.container.style.transform = `rotate(${ this.rotate }deg)`
      this.container.style.transition = 'transform .06s ease-out'
    },

    // 
    eventClear() {
      this.clearCanvas()

      this.canvas?.requestRenderAll()
    },

    // 
    eventDone() {
      this.save()
    },

    // 
    eventClearActivatedCtx() {
      this.clearActivatedCtx()
    },

    // 
    eventMoveTop() {
      this.moveY = containerEvent.moveTop(this.params)
      this.initY = this.moveY
      this.transformContainer()
    },

    // 
    eventMoveRight() {
      this.moveX = containerEvent.moveRight(this.params)
      this.initX = this.moveX
      this.transformContainer()
    },

    // 
    eventMoveBottom() {
      this.moveY = containerEvent.moveBottom(this.params)
      this.initY = this.moveY
      this.transformContainer()
    },

    // 
    eventMoveLeft() {
      this.moveX = containerEvent.moveLeft(this.params)
      this.initX = this.moveX
      this.transformContainer()
    }
  }
}