import axios from 'axios'

/**
 * @description 
 */ 
function arenaBaseUrl() {
    const devUrl = ``
    const proUrl = ``

    const env = process.env.NODE_ENV || 'development'
    
    if (env.trim() === 'development') {
        return devUrl
    }
    return proUrl
}

/**
 * @description 
 */ 
function createService() {
    //  axios 
    const service = axios.create()

    // 
    service.interceptors.request.use(
        config => {
            return config
        },
        error => {
            return Promise.reject(error)
        }
    )

    // 
    service.interceptors.response.use(
        response => {
            const dataAxios = response.data
            const { success } = dataAxios
            const code = dataAxios.code || null
            // const message = dataAxios.message || null

            if(success) {
                switch (code) {
                    case 1001: break 
                    default: break
                }
            }else {
                switch (code) {
                    case 1001: break
                    default: break
                }
            }

            dataAxios.api = response.config.url
            return dataAxios
        },
        error => {
            const status = error.response.status
            switch (status) {
                case 400: error.message = ''; break
                case 401: error.message = ''; break
                case 403: error.message = ''; break
                case 404: error.message = ''; break
                case 408: error.message = ''; break
                case 500: error.message = ''; break
                case 501: error.message = ''; break
                case 502: error.message = ''; break
                case 503: error.message = ''; break
                case 504: error.message = ''; break
                case 505: error.message = 'HTTP'; break
                default: break
            }
            return Promise.reject(error)
        }
    )

    return service
}

/**
 * @description 
 * @param {Object} service axios 
 */ 
function createRequest(service) {
    return function(config) {
        // const token = util.storage.get('token')
        const headers = {
            // Authorization: `Bearer ${ token }`,
            'Content-Type': 'application/json;charset=UTF-8'
        }
        const defaultConfig = {
            method: 'get',
            baseURL: arenaBaseUrl(),
            headers,
            data: {},
            // timeout: 3000
        }
        const assignConfig = Object.assign(defaultConfig, config)
        assignConfig.headers.Authorization = headers.Authorization
        return service(assignConfig)
    }
}

const service = createService()
const request = createRequest(service)

export default request